import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LayoutComponent } from './layout/layout/layout.component';
import {EmptyComponent} from './pages/empty/empty.component';

import {LoginResolver} from './pages/login/resolver/login-resolver';
import { NotFoundComponent } from './pages/not-found/not-found.component';
import {AuthGuard} from './pages/login/guard/auth-guard';

const routes: Routes = [
  {
    path: '',
    pathMatch: 'full',
    redirectTo: 'login',
  },
  {
    path: 'login',
    loadChildren: './pages/login/login.module#LoginModule',
    //canActivate: [AuthGuard],
  },
  {
    path: 'map',
    loadChildren: './pages/map/map.module#MapModule',
  },
  {
    path: 'medical-care',
    component: LayoutComponent,
    loadChildren: './pages/medical-care/mc.module#McModule',
    //canActivate: [AuthGuard],
  },
  {
    path: 'feedback/:code',
    loadChildren: './pages/feedback/feedback.module#FeedbackModule',
  },
  {
    path: 'auth/verification',
    component: EmptyComponent,
    resolve: { userLogged: LoginResolver},
  },
  {
    path: '**',
    component: NotFoundComponent,
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { scrollPositionRestoration: 'enabled' })],
  exports: [RouterModule],
})
export class AppRoutingModule { }
