import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'search-for-history',
    pathMatch: 'full',
  },
  {
    path: 'search-for-history',
    loadChildren: './search-medical-history/search-for.module#SearchForModule',
  },
  {
    path: 'medical-history',
    loadChildren: './medical-history/medical-history.module#MedicalHistoryModule',
  },
  {
    path: 'support',
    loadChildren: './support/support.module#SupportModule',
  },
  {
    path: 'scheduling',
    loadChildren: './scheduling/scheduling.module#SchedulingModule',
  },
  {
    path: 'profile',
    loadChildren: './profile/profile.module#ProfileModule',
  },
  {
    path: 'reports',
    loadChildren: './reports/reports.module#ReportsModule',
  },
  {
    path: 'profesional-profile',
    loadChildren: './profesional-profile/profesional-profile.module#ProfesionalProfileModule',
  },
  {
    path: 'geolocation',
    loadChildren: './geolocation/geolocation.module#GeolocationModule',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class McRoutingModule { }
