import {Injectable} from '@angular/core';
import {Attached} from '../dto/attached';
import {BodyParts} from '../dto/body-parts';
import {BodySystems} from '../dto/body-systems';
import {ConsultationData} from '../dto/consultation-data';
import {Diagnosis} from '../dto/diagnosis';
import {Economic} from '../dto/economic';
import {FamillyBackground} from '../dto/familly-background';
import {Ginecology} from '../dto/ginecology';
import {IdentificationData} from '../dto/identification-data';
import {LifeStyle} from '../dto/life-style';
import {MedicalHistory} from '../dto/medical-history';
import {Medication} from '../dto/medication';
import {PersonalBackground} from '../dto/personal-background';
import {PhysicalExam} from '../dto/physical-exam';
import {Procedure} from '../dto/procedure';
import {VitalSignsAndMeasures} from '../dto/vital-signs-and-measures';

@Injectable({
  providedIn: 'root',
})
export class BuilderService {

  constructor() { }

  public buildMedicalHistory(fg0, fg1, fg2, fg3, medications: Medication[]): MedicalHistory {
    const identificationData = new IdentificationData(
      fg0.name,
      fg0.lastName,
      fg0.docType,
      fg0.document,
      fg0.email,
      fg0.mobilePhone,
      fg0.phone,
      fg0.userType,
      fg0.maritalStatus,
      fg0.affType,
      fg0.occupation,
      fg0.birthDate,
      fg0.gender,
      fg0.city,
      fg0.zone,
      fg0.address,
      fg0.eps,
      fg0.companionName,
      fg0.companionRelationship,
      fg0.companionEmail,
      fg0.companionPhone,
      fg0.subscriptionType,
    );

    const consultationData = new ConsultationData(
      fg1.consultationReason,
      fg1.indicatedSickness,
      fg1.consultationCode,
      fg1.consultationPurpose,
      fg1.externalCause,
      new PersonalBackground(
        fg1.allergies,
        fg1.pharmacological,
        fg1.hospitalizations,
        fg1.pathological,
        fg1.surgical,
        fg1.toxicological,
        fg1.trauma,
        fg1.transfusions,
        fg1.pastAnnotation,
      ),
      new FamillyBackground(
        fg1.cerebrovascularF,
        fg1.cerebrovascularM,
        fg1.cerebrovascularB,
        fg1.cerebrovascularGF,
        fg1.cancerF,
        fg1.cancerM,
        fg1.cancerB,
        fg1.cancerGF,
        fg1.heartDiseaseF,
        fg1.heartDiseaseM,
        fg1.heartDiseaseB,
        fg1.heartDiseaseGF,
        fg1.diabetesF,
        fg1.diabetesM,
        fg1.diabetesB,
        fg1.diabetesGF,
        fg1.dyslipidemiasF,
        fg1.dyslipidemiasM,
        fg1.dyslipidemiasB,
        fg1.dyslipidemiasGF,
        fg1.hypertensionF,
        fg1.hypertensionM,
        fg1.hypertensionB,
        fg1.hypertensionGF,
      ),
      new LifeStyle(
        fg1.alcoholicDrinks,
        fg1.cigarette,
        fg1.medicines,
        fg1.hallucinogens,
      ),
      new VitalSignsAndMeasures(
        fg1.systolicBloodPressure,
        fg1.diastolicBloodPressure,
        fg1.meanArterialPressure,
        fg1.heartRate,
        fg1.breathingFrequency,
        fg1.pulseOximetry,
        fg1.temperature,
        fg1.degrees,
        fg1.glucometry,
        fg1.weight,
        fg1.height,
        fg1.imc,
        fg1.headCircunference,
        fg1.waistCircumference,
      ),
      new BodyParts(
        fg1.head,
        fg1.neck,
        fg1.abdomen,
        fg1.chest,
        fg1.extremities,
        fg1.bottomAnnotation,
      ),
      new BodySystems(
        fg1.articulate,
        fg1.collagen,
        fg1.dental,
        fg1.digestive,
        fg1.endocrine,
        fg1.lymphatic,
        fg1.muscular,
        fg1.nervous,
        fg1.osseous,
        fg1.skin,
        fg1.respiratory,
        fg1.blood,
        fg1.urinary,
        fg1.topAnnotation,
      ),
      new PhysicalExam(
        fg1.phHead,
        fg1.phEyes,
        fg1.phEars,
        fg1.phNose,
        fg1.phMouth,
        fg1.phNeck,
        fg1.phChest,
        fg1.phAbdomen,
        fg1.phGenitals,
        fg1.phExtremities,
        fg1.phSkin,
        fg1.phNeurological,
        fg1.phOcularOpening,
        fg1.phMotorResponse,
      ),

      new Ginecology(
        fg1.menarche,
        fg1.lastMenstruation,
        fg1.menopause,
        fg1.cycles,
        fg1.menstruationType,
        fg1.pregnancies,
        fg1.births,
        fg1.cesarean,
        fg1.abortions,
        fg1.livingChildren,
        fg1.deadChildren,
        fg1.sexPartners,
        fg1.startSexualLife,
        fg1.startPlanning,
        fg1.planningMethod,
        fg1.breastExam,
        fg1.menstrualFlow,
      ),
    );

    const diagnosis = new Diagnosis(
      fg1.mainDiagnosis,
      fg1.mainDiagnosisType,
      fg1.relatedDiagnosis1,
      fg1.relatedDiagnosis1Type,
      fg1.relatedDiagnosis2,
      fg1.relatedDiagnosis2Type,
      fg1.relatedDiagnosis3,
      fg1.relatedDiagnosis3Type,
    );

    const economic = new Economic(
      fg1.consultationValue,
      fg1.feeValue,
      fg1.totalValuePaidForEAPB,
    );

    const attached = new Attached(
      fg1.informedConsent,
      fg1.labs,
      fg1.images,
    );

    const procedure = new Procedure(
      fg2.procedureCode,
      fg2.performingMethod,
      fg2.procedurePurpose,
      fg2.procMainDiagnosis,
      fg2.procRelatedDiagnosis,
      fg2.complication,
    );

    return new MedicalHistory(
      identificationData,
      consultationData,
      diagnosis,
      economic,
      procedure,
      medications,
    );
  }
}
