import {Component, Input, OnInit} from '@angular/core';
import {FormGroup} from '@angular/forms';
import {ListItem} from '../../../../../core/dto/list-item';
import {combineLatest} from 'rxjs';
import {ListService} from '../../../../../core/service/list.service';

const CYCLES = 'CYCLES';
const MENSTRUATION_TYPES = 'MENSTRUATION_TYPES';

@Component({
  selector: 'ct-ginecology',
  templateUrl: './ginecology.component.html',
  styleUrls: ['./ginecology.component.scss'],
})
export class GinecologyComponent implements OnInit {

  @Input()
  public fg: FormGroup;
  public cicles: ListItem[] = [];
  public menstruationTypes: ListItem[] = [];

  constructor(
    private readonly listService: ListService,
  ) { }

  public ngOnInit() {
    this.getLists();
  }

  public async getLists() {
    combineLatest(
      this.listService.getList(CYCLES),
      this.listService.getList(MENSTRUATION_TYPES),
    ).subscribe(([ cicles, menstruationTypes]) => {
      this.cicles = cicles;
      this.menstruationTypes = menstruationTypes;
    });
  }

}
