import {Component, OnInit, ViewChild} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {MatPaginator, MatTableDataSource} from '@angular/material';
import {ListItem} from '../../../../core/dto/list-item';
import {ValueObject} from '../../../../core/dto/value-object';
import {ListService} from '../../../../core/service/list.service';
import {MedicalHistoryService} from '../../http/medical-history.service';
import {MedicalHistory} from '../../medical-history/dto/medical-history';
import {DoctypeDocument} from '../dto/doctype-document';
import {MedicalHistoryForSearch} from '../dto/medical-history-for-search';
import {PdfBuildService} from '../../medical-history/service/pdf-build.service';

const DOC_TYPES = 'DOC_TYPES';

@Component({
  selector: 'ct-search-medical-history',
  templateUrl: './search-medical-history.component.html',
  styleUrls: ['./search-medical-history.component.scss'],
})
export class SearchMedicalHistoryComponent implements OnInit {

  public dataSource = new MatTableDataSource<MedicalHistory>([]);
  // Data table
  public displayedColumns: string[] = ['nO', 'date', 'name', 'document', 'eps', 'actions'];
  @ViewChild(MatPaginator, {static: true}) public paginator: MatPaginator;

  // form
  public fg: FormGroup;

  // lists
  public docTypes: ListItem[] = [];

  constructor(
    private readonly fb: FormBuilder,
    private readonly listService: ListService,
    private readonly medicalHistoryService: MedicalHistoryService,
    public readonly pdfBuildService: PdfBuildService,
  ) { }

  public ngOnInit() {
    this.initForms();
    this.getList();
  }

  public initForms() {
    this.fg = this.fb.group({
      docType: [null, [Validators.required]],
      document: [null, [Validators.required]],
    });
  }

  public getList() {
    this.listService.getList(DOC_TYPES)
      .subscribe( (doctypes: ListItem[]) => {
        this.docTypes = doctypes;
      });
  }

  public search() {
    if (this.fg.invalid) { return; }
    this.medicalHistoryService.findByDocument(
      new DoctypeDocument(
        this.fg.get('docType').value,
        this.fg.get('document').value,
      ),
    ).subscribe((medicalHistories: MedicalHistory[]) => {
      this.dataSource = new MatTableDataSource<MedicalHistory>(medicalHistories);
    });
  }



}
