import {Location} from '@angular/common';
import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Route, Router} from '@angular/router';

interface IFeedbackMessage {
  message: string;
  reditectTo: string;
  textButton: string;
}

const messages = {
  401: {message: 'Tu sesin ha expirado, por favor vuelve a iniciar sesin', redirectTo: '/login', textButton: 'Iniciar sesin'},
  500: {message: 'Ocurri un error, por favor intenta ms tarde', redirectTo: 'BACK', textButton: 'Volver'},
};

@Component({
  selector: 'ct-feedback',
  templateUrl: './feedback.component.html',
  styleUrls: ['./feedback.component.scss'],
})
export class FeedbackComponent implements OnInit {

  public feedBack: IFeedbackMessage;

  constructor(
    private readonly actRoute: ActivatedRoute,
    private readonly router: Router,
    private location: Location,
  ) { }

  public ngOnInit() {
    const code = ['401', '500'].includes(this.actRoute.snapshot.paramMap.get('code')) ? this.actRoute.snapshot.paramMap.get('code') : 500;
    this.feedBack = messages[code];
  }

  public goTo(redirectTo: string) {
    if (redirectTo === 'BACK') {
      this.location.back();
      return;
    }

    this.router.navigateByUrl(redirectTo);
  }

}
