import { Injectable } from '@angular/core';
import {ApiService} from '../../../core/service/api.service';
import {MedicalHistory} from '../medical-history/dto/medical-history';
import {DoctypeDocument} from '../search-medical-history/dto/doctype-document';

@Injectable({
  providedIn: 'root',
})
export class MedicalHistoryService {

  public BASE_URL = '/medical-history';
  public SEARCH_FOR = '/search-for';

  constructor(private api: ApiService) { }

  public save(medicalHistory: MedicalHistory) {
    return this.api.post(this.BASE_URL, medicalHistory);
  }

  public get(id: number) {
    return this.api.get(`${this.BASE_URL}/${id}`);
  }

  public findByDocument(doctypeDocument: DoctypeDocument) {
    return this.api.post(this.BASE_URL + this.SEARCH_FOR, doctypeDocument);
  }
}
