  public generateMedicationsPDF(medicalHistory: MedicalHistory) {
    const body: any[] = [
      [{text: 'Fecha de prescripcin ', colSpan: 2, style: 'subheader'}, '', '' + this.datePipe.transform(medicalHistory.createdAt, 'd/M/yyyy'),
        {text: 'No. Frmula', style: 'subheader'}, '' + medicalHistory.id],
      [{text: 'Datos del paciente', colSpan: 5, style: 'tableHeader', alignment: 'center'}, '', '', '', ''],
      [{text: 'Documento', style: 'subheader', alignment: 'center'},
        {text: 'Nombre', colSpan: 2, style: 'subheader', alignment: 'center'}, '',
        {text: 'Apellidos', colSpan: 2, style: 'subheader', alignment: 'center'}, ''],
      [{text: medicalHistory.identificationData.docType.subValue + ' ' + medicalHistory.identificationData.document, alignment: 'center'},
        {text: medicalHistory.identificationData.name, colSpan: 2, alignment: 'center'}, '',
        {text: medicalHistory.identificationData.lastName, colSpan: 2, alignment: 'center'}, ''],
      [{text: 'Medicamento/Producto', style: 'subheader1', alignment: 'center'},
        {text: 'Dosis', style: 'subheader1', alignment: 'center'},
        {text: 'Via Administracin', style: 'subheader1', alignment: 'center'},
        {text: 'Frecuencia Administracin', style: 'subheader1', alignment: 'center'},
        {text: 'Duracin Tratamiento', style: 'subheader1', alignment: 'center'}],
    ];
    medicalHistory.medication.map((x: Medication) => {
      body.push([{text: x.medicationCode.value},
        {text: x.dose},
        {text: x.pharmaceuticalForm.value},
        {text: x.frecuency},
        {text: x.duration}]);
    });
    body.push([{text: 'Recomendacin', style: 'subheader1'}, {text: '', colSpan: 4}, '', '', '']);
    body.push([{text: 'Profesional', style: 'subheader1'},
      {text: 'Dr. ' + medicalHistory.doctor.name + ' ' + medicalHistory.doctor.lastName, colSpan: 2},
      '', {text: '', colSpan: 2, rowSpan: 3}, '']);
    body.push([{text: 'Registro', style: 'subheader1'}, {text: 'RM: ' + medicalHistory.doctor.doctorNumber, colSpan: 2}, '', '', '']);
    body.push([{text: 'Documento', style: 'subheader1'},
      {text: medicalHistory.doctor.docType.subValue + ' ' + medicalHistory.doctor.document, colSpan: 2}, '', '', '']);

    const documentDefinition = {
      content: [
        {text: 'Frmula Mdica', style: 'header', alignment: 'center'},
        {
          style: 'tableExample',
          table: {
            widths: ['*', '*', '*', '*', '*'],
            body,
          },
        },
      ],
      styles: {
        header: {
          fontSize: 18,
          bold: true,
          margin: [0, 0, 0, 10],
        },
        subheader1: {
          fontSize: 10,
          bold: true,
        },
        subheader: {
          bold: true,
        },
        tableExample: {
          margin: [20, 5, 0, 15],
        },
        tableHeader: {
          fillColor: '#e6f9ff',
          bold: true,
          fontSize: 13,
          color: '#004666',
        },
      },
      defaultStyle: {
        // alignment: 'justify'
      },
    };

    pdfMake.createPdf(documentDefinition).open();
  }

