import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {map} from 'rxjs/operators';
import {LocalStorageService} from '../../core/service/local-storage.service';
import {UserLogged} from '../login/dto/user-logged';

@Component({
  selector: 'ct-empty',
  templateUrl: './empty.component.html',
  styleUrls: ['./empty.component.scss'],
})
export class EmptyComponent implements OnInit {

  public user: UserLogged;
  public rt = 'rt';
  public at = 'at';

  constructor(
    private readonly activatedRoute: ActivatedRoute,
    private readonly ls: LocalStorageService,
  ) { }

  public ngOnInit(): void {
    this.user = this.activatedRoute.snapshot.data.userLogged as UserLogged;
    if (this.user.refreshToken && this.user.accessToken) {
      this.ls.setItem(this.rt, this.user.refreshToken);
      this.ls.setItem(this.at, this.user.accessToken);
      window.location.href = '/medical-care/search-for-history';
    } else {
      window.location.href = '/login';
    }
  }

}
