import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { SchedulingRoutingModule } from './scheduling-routing.module';
import { MatButtonModule, MatToolbarModule, MatInputModule, MatStepperModule, MatDatepickerModule, MatNativeDateModule, MatAutocompleteModule, MatSelectModule, MatCheckboxModule, MatIconModule, MatSnackBarModule } from '@angular/material';
