import {Component, Input, OnInit} from '@angular/core';
import {FormGroup} from '@angular/forms';
import {combineLatest} from 'rxjs';
import {ListItem} from '../../../../../core/dto/list-item';
import {ListService} from '../../../../../core/service/list.service';

const CUPS_CODES = 'CUPS_CODES';
const PURPOSES = 'PURPOSES';
const EXTERNAL_CAUSES = 'EXTERNAL_CAUSES';
const MIN_SEARCH_LENGTH = 4;

@Component({
  selector: 'ct-cd-description',
  templateUrl: './cd-description.component.html',
  styleUrls: ['./cd-description.component.scss'],
})
export class CdDescriptionComponent implements OnInit {

  @Input()
  public fg1: FormGroup;
  public cupsCodes: ListItem[] = [];
  public purposes: ListItem[] = [];
  public externalCauses: ListItem[] = [];

  constructor(
    private readonly listService: ListService,
  ) { }

  public ngOnInit() {
    this.getLists();
  }

  public async getLists() {
    combineLatest(
      this.listService.getList(PURPOSES),
      this.listService.getList(EXTERNAL_CAUSES),
    ).subscribe(([purposes, externalCauses]) => {
      this.purposes = purposes;
      this.externalCauses = externalCauses;
      this.startSubscriptions();
    });
  }

  private startSubscriptions() {
    this.fg1.get('consultationCode').valueChanges.subscribe(
      (value) => {
        if (value.length < MIN_SEARCH_LENGTH || typeof value !== 'string') {return; }
        this.listService.getListByKeyAndValue(CUPS_CODES, value)
          .subscribe(((listItems) => {
            this.cupsCodes = listItems;
          }));
      },
    );
  }

  public displayProperty(value: ListItem): string {
    if (value) {
      return value.value;
    }
  }

}
